export function copyToClipboard(text, button) {
  navigator.clipboard.writeText(text).then(() => {
    const originalText = button.textContent;
    button.textContent = "Copied!";
    button.style.backgroundColor = "green";
    setTimeout(() => {
      button.textContent = originalText;
      button.style.backgroundColor = "";
    }, 500);
  });
}

export async function pasteFromClipboard(handler) {
  try {
    const text = await navigator.clipboard.readText();
    if (typeof handler === "function") {
      handler(text);
    } else if (handler && "value" in handler) {
      handler.value = text;
    }
  } catch (err) {
    if (err.name === "NotAllowedError") {
      try {
        await navigator.permissions.query({ name: "clipboard-read" });
        const text = await navigator.clipboard.readText();
        if (typeof handler === "function") {
          handler(text);
        } else if (handler && "value" in handler) {
          handler.value = text;
        }
      } catch (permError) {
        console.error("Clipboard permission denied:", permError);
        alert("Please allow clipboard access to use paste functionality.");
      }
    } else {
      console.error("Failed to read clipboard contents:", err);
    }
  }
}
