// src/utils/dependencyLoader.js
import CodeMirror from "codemirror";
import "codemirror/mode/sql/sql";
import "codemirror/mode/xml/xml";
import "codemirror/theme/material.css";
import "codemirror/lib/codemirror.css";

import Handsontable from "handsontable";
import "handsontable/dist/handsontable.full.css";

import JSZip from "jszip";

export class DependencyLoader {
  static async loadAll() {
    // Initialize CodeMirror global
    window.CodeMirror = CodeMirror;

    // Initialize Handsontable global
    window.Handsontable = Handsontable;

    // Initialize JSZip global
    window.JSZip = JSZip;

    // Any additional initialization can go here
    return Promise.resolve();
  }

  static async load(dependency) {
    switch (dependency) {
      case "codemirror":
        return Promise.resolve(CodeMirror);
      case "handsontable":
        return Promise.resolve(Handsontable);
      case "jszip":
        return Promise.resolve(JSZip);
      default:
        throw new Error(`Unknown dependency: ${dependency}`);
    }
  }
}
